.chosen-service {
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    align-items: center;
    width: 100%;
    max-height: 340px;
}

.chosen-service {
    display: flex;
    flex-direction: row;
    align-items: center;
    width: 100%;
}

.chosen-service p {
    font-size: 14px;
    line-height: 24px;
    color: #000000;
}

.cancel {
    margin: 4px 8px;
}

.cancel:hover {
    opacity: 50%;
    cursor: pointer;
}
